<template>
    <section class="main-content">
        <link rel="stylesheet"
            href="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.5.2/css/bootstrap.min.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/custom.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/tree.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/DropZone.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/custom_cli.css" />
        <link rel="stylesheet" href="https://talygenv.github.io/Vue-UI-Content/css/default.css" />
        <loader :is-visible="isLoading"></loader>
        <div class="col-12 float-left p-0 initialsetuptabsbase" v-if="isShowInitialSetup">
            <div class="left-menu-tab new_tab">
                <div class="col-md-12 pb-0 float-left mt-2">
                    <div class="mx-auto initialsetup_vjs">
                        <div class="new_initialsetup_base m-0">
                            <div class="bg-white">
                                <div class="col-12 float-left p-0  text-center">
                                    <div class="col-lg-12  p-0">
                                        <img
                                            src="https://talygenv.github.io/Vue-UI-Content/images/initialsetup_welcome.png" />
                                    </div>
                                    <div class="col-lg-12 p-0 new_initialsetup_content mt-0 mb-3">
                                        <h3>{{ $t('Hello') }} <span class="font-weight-bold">{{ UserName }}!</span></h3>

                                        <h6 v-if="registerdFrom && registerdFrom.toLowerCase() === 'silentapp'"
                                            class="modal-title mb-2 text-capitalize"
                                            v-html="$t('WelcomeToTheSilentAppFamily')">
                                        </h6>

                                        <h6 v-else-if="registerdFrom && registerdFrom.toLowerCase() === 'trackedtime'"
                                            class="modal-title mb-2 text-capitalize"
                                            v-html="$t('WelcomeToTheTrackedTimeFamily')">
                                        </h6>

                                        <h6 v-else-if="registerdFrom && registerdFrom.toLowerCase() === 'asset'"
                                            class="modal-title mb-2 text-capitalize"
                                            v-html="$t('WelcomeToTheAssetFamily')">
                                        </h6>

                                        <h6 v-else-if="registerdFrom && registerdFrom.toLowerCase() === 'hiring'"
                                            class="modal-title mb-2 text-capitalize"
                                            v-html="$t('WelcomeToTheHiringFamily')">
                                        </h6>

                                        <h6 v-else class="modal-title mb-2 text-capitalize"
                                            v-html="$t('WelcomeToTheTalygenFamily')">
                                        </h6>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-md-12 mb-4 float-left">
                    <div class="row">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                            <div class="row">
                                <div class="col-8 pr-0 font-weight600 mb-1">{{ $t('setupyouraccount') }}</div>
                                <div class="col-4 text-right">
                                    <b class="">
                                        <span class="smallresult" v-if="setupProgresText !== 'Completed'">{{
                                            $t('Progress') }} <span class="progress_green">{{ currentTabIndex }}</span>
                                            {{ $t('of') }} <span class="progress_blue"> {{ TabList.length
                                                }}</span></span>
                                    </b>
                                    <span class="smallresult" v-if="setupProgresText == 'Completed'">
                                        <i class="fa fa-check-circle"
                                            v-bind:class="{ 'text-success': setupProgresText == 'Completed', 'text-muted': setupProgresText !== 'Completed' }"></i>
                                        {{ $t(setupProgresText) }}
                                    </span>
                                </div>
                                <div class="col-4 pl-0 text-right">

                                </div>
                            </div>
                            <div class="initialsetup_newtab_design progress storagegraph smaller">
                                <div class="progress-bar progress-bar-primary px-0 text-right memoryused"
                                    id="divConsumendStorage" :class="`${progressBarColor}`"
                                    :style="`width: ${setupProgress}%; font-weight:500; font-size:14px`"
                                    :title="setupProgress">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="divscrollsetuptabs" class="mt-3">
                    <ul
                        class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix initialsetup_newtab_design">
                        <li v-if="item.displayName !== 'SetupAppCategory'"
                            :class="{ 'nav-item FirstScreen': true, 'active': item.tabCode == currentSelectedTab.tabCode, 'initialsetup_completed': item.isCompleted }"
                            v-for="(item, index) in TabList" :key="index" v-on:click="setCurrectTab(item)">
                            <a class="digits_num">{{ (index + 1) }}.</a>
                            <a :class="{ 'nav-link': true }" style="cursor: pointer;">
                                <i class="fa fa-check-circle text-success" v-if="item.isCompleted"></i>
                                <i :class="'pr-2 text-muted ' + item.icon" v-if="!item.isCompleted"></i>
                                <span>{{ $t(item.displayName) }}</span>
                            </a>
                        </li>

                    </ul>
                </div>



                <div class="notebox mt-4">
                    <div>
                        <h6 style="margin-bottom: 0.5em;"><i class="fa fa-sticky-note" aria-hidden="true"></i>
                            {{ $t('setupNote') }}
                        </h6>
                        <p style="margin-bottom: 0;">{{ $t('youcanskip') }} <u>
                                <span style="cursor: pointer; color: black; font-weight: 600;"
                                    v-on:click="GotoHome()">{{ $t('click_here') }}</span>
                            </u>
                            {{ $t('tojumpstraightto') }}
                        </p>
                    </div>
                </div>
                <div class="sevendaybox mt-4">
                    <div class="col-md-12 float-left p-0">
                        <div class="col-md-8 float-left p-0">
                            <h1 class="font-20 mb-1">{{ traildaysleft }} {{ $t('daysLeft') }}</h1>
                            <h2 class="font-16 font-weight-300">{{ $t('inyourfreetrial') }}</h2>
                        </div>
                        <div class="col-md-4 float-right p-0 text-center">
                            <div class="set-size charts-container float-right">
                                <div class="pie-wrapperbase">
                                    <div class="pie-wrapper pie-wrapper--solid progress-88">
                                        <span class="label">{{ $t('88') }}<span class="smaller">{{ $t('Percentagesign')
                                                }}</span></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-12 float-left font-14 p-0 font-weight-400">{{ $t('continueleveragingvaluable') }}
                    </div>

                    <div class="col-12 float-left text-center p-0 mt-3">
                        <!-- <a :href="appURL + '/Billing/Index?reqhFrom=0'" class="btn btn-warning w-100 font-weight-medium text-uppercase font-16">{{$t('upgradeyourplan') }}</a> -->

                        <a :href="appURL" class="btn btn-warning w-100 font-weight-medium text-uppercase font-16">{{$t('upgradeyourplan') }}</a>
                    </div>
                </div>


            </div>
            <div class="right-content">
                <div class="mainheader_app">
                    <img v-if="registerdFrom && registerdFrom.toLowerCase() == 'silentapp'"
                        src="https://talygenv.github.io/Vue-UI-Content/images/logo_silentapp.png">
                    <img v-else-if="registerdFrom && registerdFrom.toLowerCase() == 'trackedtime'"
                        src="https://talygenv.github.io/Vue-UI-Content/images/logo_trackedtime.png">
                    <img v-else-if="registerdFrom && registerdFrom.toLowerCase() == 'asset'"
                        src="https://talygenv.github.io/Vue-UI-Content/images/logo_assetmanagement.png">
                    <img v-else-if="registerdFrom && registerdFrom.toLowerCase() == 'hiring'"
                        src="https://talygenv.github.io/Vue-UI-Content/images/hired-logo.png">
                    <img v-else src="https://talygen.com/content/images/index/logo_newdesign.png">
                    <div class="float-right text-white">
                        <a @click="logOut" class="text-white text-center p-action-btn-top"><em
                                class="fa fa-sign-out"></em> <br /> {{ $t('Logout') }}</a>
                    </div>
                    <div class="float-right text-white">
                        <a class="text-white text-center p-action-btn-top" v-on:click="GotoHome()"><em
                                class="fa fa-home"></em> <br /> {{ $t('Home') }}</a>
                    </div>
                    <div class="float-right text-white"
                        v-if="currentSelectedTab.componentName == 'DownloadSilentApp' || currentSelectedTab.componentName == 'CreateUser' || currentSelectedTab.componentName == 'DeviceMapping'">
                        <a :class="{ 'text-white text-center p-action-btn-top bulb': true, 'active': isShowTips }"
                            v-on:click="onClickTips(currentSelectedTab.componentName)"><em></em>
                            <br />{{ $t('quicktips') }}</a>
                        <div class="content_plate" v-if="isShowTips">{{ tipsContent }}</div>

                    </div>
                    <!-- <h3 class="float-right mr-5 text-white mt-4">{{ HeaderText }}</h3> -->

                </div>
                <div class="tab-content" v-if="currentSelectedTab != null">
                    <moduleSetupBridge :component="currentSelectedTab.componentName"
                        :isCompleted="currentSelectedTab.isCompleted" :moduleCode="moduleCode"
                        v-on:PreviousTab="PreviousTab" v-on:UpdateProgress="UpdateProgress" v-on:NextTab="NextTab"
                        v-on:Close="Close" :tabCount="TabList.length" :currentTabIndex="currentTabIndex"
                        reqFrom="InitialSetup"></moduleSetupBridge>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from "@/services/DataService";
import moduleSetupBridge from "../AdvanceSetup/moduleComponents/moduleSetupBridge.vue";
import authservice from "../../services/authservice";
export default {
    components: {
        moduleSetupBridge
    },
    data() {
        return {
            isLoading: false,
            isShowInitialSetup: false,
            UserName: '',
            setupTabs: [],
            setupProgress: 0,
            TabList: [],
            currentSelectedTab: null,
            setupProgresText: 'Inprogress',
            progressBarColor: "",
            moduleCode: "Timer",
            subModuleCode: "SILENT_APP",
            traildaysleft: 0,
            //appURL: process.env.VUE_APP_DEFAULT_APP_URL,
            isInitialSetup: false,
            userTypeId: 3,
            tipsContent: '',
            isShowTips: false,
            registerdFrom: ''

        }
    },
    watch: {
        setupTabs: function (val) {

            this.TabList = val;
            if (val.length > 0) {
                this.currentSelectedTab = val[0];
            }
        }
    },
    async created() {

        var vm = this;

        if (process.env.VUE_APP_DEFAULT_APP_URL == 'https://appone.talygen.com') {

            vm.appURL = 'https://appone.talygen.com/Billing/Index?reqhFrom=0';

        } else {
            vm.appURL = 'https://stage.talygen.com/ManageSubscription/Index';

        }
        vm.isLoading = true;
        vm.userTypeId = vm.GetUserInfo.UserType == 'CA' ? 1 : vm.GetUserInfo.UserType == 'PM' ? 2 : vm.GetUserInfo.UserType == 'NU' ? 3 : 4;
        await vm.GetUserSettingConfigByType();
        if (!(!vm.isInitialSetup && vm.GetUserInfo && vm.GetUserInfo.IsDefault)) {
            window.location.href = '/User/Home';
        }
        else {
            await vm.StartInitialSetup();
            await vm.ImportSampleData();
            await vm.GetFromCompanyRegistration();
            vm.UserName = vm.GetUserInfo.Name.split(' ')[0];
            await vm.GetProgresSetupStatusDetails();
            await vm.GetRemainingFreeTrailDays();
            vm.isShowInitialSetup = true;
        }
        vm.isLoading = false;
    },
    computed: {
        currentTabIndex: function () {

            return (this.TabList.indexOf(this.currentSelectedTab) + 1)
        },
        HeaderText: function () {

            return (this.currentSelectedTab ? this.$t(this.currentSelectedTab.displayName) : '')
        }
    },
    methods: {
        async GetRemainingFreeTrailDays() {
            var vueObj = this;
            await DataService.GetRemainingFreeTrailDays().then((response) => {
                vueObj.traildaysleft = response.data < 0 ? 0 : response.data;
            }
            );
        },
        GetProgresSetupStatusDetails: async function () {

            var ths = this;
            ths.setupProgress = 0;
            var url = ''
            if (ths.moduleCode && ths.moduleCode.toLowerCase() == 'timer') {
                url = `moduleName=${ths.moduleCode}&subModuleCode=${ths.subModuleCode}&usedFor=INITIAL_SETUP`
            }
            else {
                url = `moduleName=${ths.moduleCode}&subModuleCode=&usedFor=`
            }
            ths.setupTabs = [];
            await DataService.GetProgresSetupStatusDetail(url).then(function (response) {
                if (response && response.data != null && response.data.status == "Success") {
                    var data = response.data.result;
                    var weightage = Math.ceil((100 / data.length));
                    data.forEach(function (item) {
                        ths.setupTabs.push({
                            tabCode: item.COMPONENT_NAME,
                            componentName: item.COMPONENT_NAME,
                            displayName: item.DISPLAY_NAME,
                            icon: item.ICON,
                            isCompleted: item.IS_COMPLETED
                        });
                        if (item.IS_COMPLETED == true) {
                            if ((ths.setupProgress + weightage) < 100) {
                                ths.setupProgress = + ths.setupProgress + weightage;
                            } else {
                                ths.setupProgress = 100;
                            }
                        }
                    })
                }
            });
        },
        GetProgresSetupStatusDetail: function () {
            var ths = this;
            var weightage = (100 / ths.setupTabs.length).toFixed(2);
            ths.setupProgress = 0;
            ths.setupTabs.forEach(function (item) {
                if (item.isCompleted == true) {
                    if ((ths.setupProgress + parseFloat(weightage)) < 100) {
                        ths.setupProgress += parseFloat(weightage);
                    }
                    //  else{
                    //     ths.setupProgress = 100;
                    // }
                    if (ths.setupProgress == 100) {
                        ths.setupProgresText = 'Completed';
                        ths.progressBarColor = "bg-success";
                    }
                }
            })
        },
        async ImportSampleData() {
            await DataService.GetImportSampleData().then(function (response) {
            })
        },
        async StartInitialSetup() {
            await DataService.StartInitialSetup().then(function (response) {
            })
        },
        setCurrectTab: function (item, index) {

            var ths = this;
            ths.isShowTips = false;
            var index = this.TabList.findIndex(function (tab) {
                return tab === item;
            });

            if (index === 0) {
                ths.currentSelectedTab = item;
                return;
            }

            // for(var i=1; i < this.TabList.length; i++){
            //      if(!this.TabList[i].isCompleted){                    
            //         ths.ShowAlert(`Please complete the step ${i+1} before proceeding.`, "warning", true);
            //         return false;
            //     }
            // }

            if (!this.TabList[index - 1].isCompleted && this.TabList[index - 1].componentName != 'DeviceMapping' && (ths.moduleCode && ths.moduleCode.toLowerCase() == 'timer' && ths.subModuleCode && ths.subModuleCode.toLowerCase() == 'silent_app')) {
                ths.ShowAlert(`Please complete the step ${index} before proceeding.`, "warning", true);
                return false;
            }

            if (index > 1 && !this.TabList[index - 2].isCompleted && this.TabList[index - 1].componentName == 'DeviceMapping' && (ths.moduleCode && ths.moduleCode.toLowerCase() == 'timer' && ths.subModuleCode && ths.subModuleCode.toLowerCase() == 'silent_app')) {
                ths.ShowAlert(`Please complete the step ${index - 1} before proceeding.`, "warning", true);
                return false;
            }


            if (typeof (item) != 'undefined') {
                ths.currentSelectedTab = item;
            }
        },
        NextTab: function (payload) {

            var ths = this;
            var index = this.TabList.indexOf(ths.currentSelectedTab)
            if (index > -1 && this.TabList.length > (index + 1)) {
                if (!payload.skip) {
                    this.TabList[index].isCompleted = true;
                }
                ths.currentSelectedTab = this.TabList[index + 1];
            }
            this.GetProgresSetupStatusDetail();
        },


        UpdateProgress: function (index, status) {
            this.TabList[(index - 1)].isCompleted = true;
            this.GetProgresSetupStatusDetail();
        },
        PreviousTab: function () {

            var ths = this;
            var index = this.TabList.indexOf(ths.currentSelectedTab);
            if (index > -1 && this.TabList.length >= (index + 1)) {
                ths.currentSelectedTab = this.TabList[index - 1];
            }
        },
        Close() {
        },
        async GetUserSettingConfigByType() {
            var vm = this;
            let url = `type=initialstartup`
            await DataService.GetUserSettingConfigByType(url).then((response) => {
                vm.isInitialSetup = response.data;
            });
        },
        async GetFromCompanyRegistration() {

            var vm = this;
            let url = `companyId=${vm.GetUserInfo.CompanyId}`;
            await DataService.GetUserCompanyType(url).then(async (response) => {
                vm.registerdFrom = response.data;
                // vm.registerdFrom="crm"
                vm.subModuleCode = '';

                if (vm.registerdFrom && vm.registerdFrom.toLowerCase() === "silentapp") {
                    vm.moduleCode = 'Timer';
                    vm.subModuleCode = 'SILENT_APP';
                } else if (vm.registerdFrom && vm.registerdFrom.toLowerCase() === "trackedtime") {
                    vm.moduleCode = 'Timer';
                    vm.subModuleCode = 'TIME_TRACK';
                } else if (vm.registerdFrom && vm.registerdFrom.toLowerCase() === "asset") {
                    vm.moduleCode = 'Asset';
                } else if (vm.registerdFrom && vm.registerdFrom.toLowerCase() === "hiring") {
                    vm.moduleCode = 'Hiring';
                }
                else {
                    await vm.GetModuleList();
                }
            })
        },
        GetModuleList: async function () {

            var vm = this;
            var url = '';
            url = `userTypeId=${vm.userTypeId}&isForAdvanceSetup=true&isCustomFieldEnable=1&requestFrom=InitialSetup`

            await DataService.GetModuleList(url).then(function (response) {
                if (response && response != null && response.data != null) {
                    var modulelist = response.data.Data;
                    if (modulelist.length > 0) {
                        var initialArray = ['project', 'asset', 'billing', 'crm', 'docstorage', 'expenses', 'hiring', 'ticketing', 'timer'];
                        var moduleObject = modulelist.filter(x =>
                            initialArray.some(keyword => x.module_name.toLowerCase().includes(keyword))
                        );
                        if (moduleObject.length > 0) {
                            vm.moduleCode = moduleObject[0].module_name;
                            if (vm.moduleCode.toLowerCase() == 'timer') {
                                vm.subModuleCode = 'TIME_TRACK';
                            }
                        }
                        else {
                            window.location.href = '/User/Home';
                        }
                    }
                    else {
                        vm.moduleCode = 'Timer';
                        vm.subModuleCode = 'SILENT_APP';
                    }
                }
            });
        },
        onClickTips(component) {
            var vm = this;
            vm.isShowTips = vm.isShowTips ? false : true;
            vm.tipsContent = '';
            if (component == 'DownloadSilentApp') {
                vm.tipsContent = 'You can download the application by going to the Download page and clicking on Full Menu -> SilentApp -> SilentApp Download.';
            }
            else if (component == 'CreateUser') {
                vm.tipsContent = 'You can create a new user from the application later by going to Full Menu -> Company Setup -> Users -> Add User.';
            } else if (component == 'DeviceMapping') {
                vm.tipsContent = 'You can map multiple devices. To map a new device, go to Full Menu -> Silent App -> Device Mapping.';
            }
        },
        async GotoHome() {
            var vm = this;
            vm.confirmR(vm.$t('SkipAllTabsConfirmation'), true, vm.$t("Confirm"), async function () {
                await vm.UpdateUserSettingConfig('HideContextmenu');
                await vm.UpdateUserSettingConfig('initialstartup');
                window.location.href = '/User/Home';
            });
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
        async logOut() {
            await authservice.logOut();
        }
    }
}
</script>
<style>
