    font-size: 13px;
    color: #fff;
    border: 1px solid #8e7217;
    -webkit-box-shadow: 0px 4px 6px -5px rgba(255, 192, 35, 1);
    -moz-box-shadow: 0px 4px 6px -5px rgba(255, 192, 35, 1);
    box-shadow: 0px 4px 6px -5px rgba(255, 192, 35, 1);
}

